<template>
  <div id="box" class="default-box">
    <!-- 边框线 start -->
    <div class="box" :class="isBigBox ? 'big-box' : 'small-box'">
      <div class="box-left"></div>
      <div class="box-left-bright"></div>
      <div class="box-right"></div>
      <div class="box-right-bright"></div>
      <div class="box-top-right"></div>
      <div class="box-top-left"></div>
      <div class="box-bottom-left"></div>
      <div class="box-bottom-right"></div>
    </div>
    <!-- 边框线 end -->

    <!-- 边框内容 start -->
    <div class="content">
      <slot></slot>
    </div>
    <!-- 边框内容 end -->
  </div>
</template>

<script>
export default {
  name: "Box",
  props: {
    // 是否外面大边框
    isBigBox: {
      type: Boolean,
      default: true
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
