-->
<template>
  <div class="system-config">
    <box>
      <!-- 头部信息 start -->
      <div class="head">
        <div class="title-box">
          <div class="title">填写相关信息</div>
        </div>
        <div class="line-box">
          <div class="triangle"></div>
        </div>
      </div>
      <!-- 头部信息 end -->
      <!-- 表单信息 start -->
      <a-form-model :model="formData" layout="inline" class="form-main">
        <a-form-model-item label="系统名称：">
          <a-input
            class="default-input"
            :disabled="!isUpdate"
            v-model="formData.systemName"
            placeholder="请输入系统名称"
          />
        </a-form-model-item>
      </a-form-model>
      <!-- 表单信息 end -->
      
      <!-- 保存和重置按钮 start -->
      <div class="btn-box" v-show="!isUpdate">
        <a-button type="primary" class="form-button" @click="setIsUpdate(true)"
          >修改</a-button
        >
      </div>

      <div class="btn-box" v-show="isUpdate">
        <a-button type="default" class="form-button" @click="setIsUpdate(false)"
          >取消</a-button
        >
        <a-button type="primary" class="form-button" @click="updateConfig"
          >确定</a-button
        >
      </div>
      <!-- 保存和重置按钮 end -->
    </box>
  </div>
</template>

<script>
import Box from "../../components/Box"; //边框样式组件

export default {
  components: {
    Box
  },
  data() {
    return {
      isUpdate: false, // 是否可以修改
      formData: {
        systemName: "智慧公安检查站管控系统"
      }
    };
  },
  methods: {
    /**
     * 设置是否可以修改信息
     * @param {*} isUpdate
     * @return {*}
     */

    setIsUpdate(isUpdate) {
      this.isUpdate = isUpdate;
    },

    /**
     * 修改配置
     * @param {*}
     * @return {*}
     */

    updateConfig() {
      let self = this;
      this.$loading.show();
      setTimeout(() => {
        self.$loading.hide();
        self.setIsUpdate(false);
        self.$message.success("修改成功");
      }, 3000);
    }
  }
};
</script>

<style lang="less">
@import "./index";
</style>
